


















































import { Component, Vue, Prop } from 'vue-property-decorator';

import webUtils from '../utils/WebUtils';
@Component
export default class Timings extends Vue {

  @Prop()
  public isMobile! :boolean;
  @Prop()
  public lang!:string;
  public mob = false;
  mounted(){
      console.log("Current day is : " + new Date().getDay());
      this.mob = webUtils.isMobile(this);
  }

  getDayColor(dayIndex:number):string{
      let curDay = new Date().getDay();
      let color = 'primary';
      if (dayIndex===curDay){
          let targetDay = this.days.filter(d => d.value===dayIndex)[0];
          if (targetDay.time[0]==='txt_Closed') color = '#970808';
          else color ='green';
      }
      return color;
  }

  public days= [
        {
          value:1,
          name: 'day_Mon',
          time: ['09:00 - 13:00','15:00 - 18:00'],
          color: 'primary lighten-1',
        },
        {
          value:2,
          name: 'day_Tus',
          time: ['09:00 - 13:00','15:00 - 18:00'],
          color: 'primary lighten-1',
        },
        {
            value:3,
          name: 'day_Wed',
          time: ['09:00 - 13:00'],
          color: 'primary lighten-1',
        },
        {
            value:4,
          name: 'day_Thu',
          time: ['09:00 - 13:00','15:00 - 18:00'],
          color: 'green',
        },
        {
            value:5,
          name: 'day_Fri',
          time: ['09:00 - 13:00'],
          color: 'primary lighten-1',
        },
        {
            value:6,
          name: 'day_Sat',
          time: ['txt_Closed'],
          color: 'primary lighten-1',
        },

        {
            value:0,
          name: 'day_Sun',
          time: ['txt_Closed'],
          color: 'primary lighten-1',
        },


];
}
